@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
footer {
  padding: 1.3em;

  background: var(--background);
  color: var(--white);
}

footer span {  
  display: -webkit-flex;  
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: var(--white);
}


header {
  display: -webkit-flex;
  display: flex;
  background: var(--background);
  color: var(--white);
}

.logoContainer {
  display: -webkit-flex;
  display: flex;
  margin: 0.25rem;  
  padding: 0.75rem;
}

.logoContainer strong {
  font-size: 1.45rem;
  font-weight: 500;
  color: var(--white);
}
.container {
  display: grid;
  place-items: center;  
  margin-top: 20px;

  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); */

  /* display: flex;
  justify-content: center; */

}

.box {
  border: solid 1px gainsboro;
  border-radius: 5px;
  min-width: 35%;
  max-width: 50%;
}

.box h2 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 20px;
}

.formCad {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px 25px;
}

.formCad input,select {
  position: relative;
  margin-top: 10px;
  border: solid 1px gainsboro;
  border-radius: 5px;
  height: 2.5rem;  
  text-indent: 10px;
  font-size: medium;
  outline-color: #94c0e4c9;
}

.formButtons button {
  margin-right: 10px;
  cursor: pointer;
  border: solid 1px gainsboro;
  border-radius: 5px;
  padding: 10px 20px;
  color: white;
  font-size: medium;
}

.formButtons button:first-child {
  background: #28a745;
}

.formButtons button:nth-child(2) {
  background: #bd2130;
}

.containerTable {
  margin: 50px auto;
  border-radius: 3px;
  width: 60%;  

  font-size: 13px;
  border: solid 1px;
}

.tableTitle {
  padding: 16px 30px;
  background: #299be4;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.row .title {
  position: relative;
  color: var(--white);
  font-size: 24px;
}

.row  button {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 36px;
  padding: 10px;
  border-radius: 2px;
  border: none;
  outline: none;
  background: #fff;
  color: #566787;
  cursor: pointer;
  font-family: "Varela Round", sans-serif;
  font-size: 13px;
}

.iconButton {
  font-size: 19px;
  margin-right: 10px;
  margin-bottom: 5px;
  cursor: pointer; 
}

table { 
  border-collapse: collapse;
  width: 100%;
  text-align: left;
  color: #212529;
}

table tr th,
table tr td {
  border-color: #e9e9e9;
  padding: 12px 15px;
  font-family: 'Varela Round', sans-serif;
}

table tr td {
  border-top: 2px solid #dee2e6;
}

table tr th:first-child {
  width: 60px;
}

table tr th:last-child {
  width: 100px;
}

table tbody tr:nth-of-type(odd) {
  background-color: #fcfcfc;
}

table tbody tr:hover {
  background: #f5f5f5;
}

.tableBottom {
  padding: 10px 5px;
  background: gainsboro;
}

.hintText {
  position: absolute;
  margin: 10px;

  font-family: "Varela Round",sans-serif;
  font-size: 15px;
  font-weight: bold;
}

.pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  
  list-style: none;
  border-radius: .25rem;
}

.pagination .pageItem {
  box-sizing: border-box;
  display: list-item;
}

.pagination .pageLink {
  position: relative;
  display: block;
  padding: .5rem .75rem;
  line-height: 1.25;
  background-color: #fff;
  border: 1px solid #dee2e6;
}

.pagination li a {
  text-decoration: none;
  background-color: transparent;

  font-size: 13px;
  min-width: 30px;
  min-height: 30px;
  color: #999;
  margin-right: 10px;
  line-height: 30px;
  border-radius: 2px !important;
  text-align: center;
  padding: 0 6px;
}

.pagination li a:hover {
  color: #666;
}

.pagination li.active a,
.pagination li.active a.pageLink {
  background: #03A9F4;
}

.pagination li.active a:hover {
  background: #0397d6;
}

.pagination li.disabled i {
  color: #ccc;
}

.pagination li i {
  font-size: 16px;
  padding-top: 6px
}

.pageItem.active .pageLink {
  z-index: 3;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.pagination li.active a, .pagination li.active a.pageLink {
  background: #03a9f4;
}

.pageLink:hover {
  z-index: 2;
  color: #0056b3;
  text-decoration: none;
  background-color: #e9ecef;
  border-color: #dee2e6;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
}

:root {
  --white: #fff;
  --background: #343a40;
}

.pageContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}
.contentWrap {
  -webkit-flex: 1 1;
          flex: 1 1;
}


